$gilroy: "Gilroy", sans-serif;
$bebas: "Bebas Neue", sans-serif;
$m_font: "Open Sans", sans-serif;
$green: #15B163;
$shadow: 0 3px 20px rgba(0,0,0,.2);
$shadow2: 0 3px 10px rgba(0,0,0,.2);

html {
	overflow-x: hidden; }

*,
*::after,
*::before {
	box-sizing: border-box; }

body {
	margin: 0;
	font-family: $m_font;
	color: #000;
	font-size: 14px;
	line-height: 1.2;
	overflow-x: hidden; }

h1,h2,h3,h4,h5,h6,p {
	margin: 0; }

ul, li {
	margin: 0;
	padding: 0;
	list-style: none; }

a {
	color: inherit;
	display: block;
	text-decoration: none;
	transition: all .3s ease; }

input, textarea, button, select {
	font-family: $m_font;
	border: 0; }

img {
	height: auto;
	width: auto;
	max-width: 100%; }

.title-underline {
	// text-transform: uppercase
	font-size: 36px;
	font-family: $bebas;
	position: relative;
	display: inline-block;
	z-index: 1;
	background-color: #fff;
	&:after {
		content: "";
		width: 103%;
		height: 5px;
		display: block;
		position: absolute;
		bottom: 0;
		left: -3px;
		background-color: rgba(21, 177, 99,.6); } }

// Button
.btn {
	background-color: $green;
	font-family: $bebas;
	border: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	color: #fff;
	font-size: 20px;
	cursor: pointer;
	box-shadow: $shadow;
	font-weight: 700; }

.buy-btn {
	width: 129px;
	height: 50px;
	font-size: 26px;
	box-shadow: none; }

.leave-review__btn {
	position: absolute;
	bottom: -25px;
	left: 50%;
	// transform: translateX(-50%)
	height: 50px;
	width: 145px;
	font-size: 18px;
	box-shadow: none; }


// Header
.header {
	padding: 39px 0;
	background-color: #fff;
	border-bottom: 1px solid $green;
	top: 0;
	right: 0;
	width: 100%;
	min-height: 150px;
	max-height: 150px; }



.nav__link {
	font-weight: 700;
	font-size: 14px;
	transition: all .3s ease;
	margin-right: 30px;
	display: block;
	position: relative;
	&::after {
		content: "";
		display: block;
		height: 3px;
		background: $green;
		position: absolute;
		bottom: -10px;
		border-radius: 15px;
		right: 0;
		width: 0;
		transition: all .3s ease; }
	&:last-child {
		margin-right: 0; }
	&:hover {
		color: $green;
		&::after {
			left: 0;
			width: 100%; } } }

.nav {
	margin-bottom: 30px;
	display: flex; }

.header__search {
	display: block;
	width: 100%;
	padding-bottom: 7px;
	outline: none;
	font-family: $m_font;
	border:  1px solid $green;
	border-radius: 5px;
	padding: 8px 0 8px 40px;
	font-weight: 500;
	background: url("../img/path1.png") no-repeat;
	background-size: auto 40%;
	background-position-x: 2%;
	background-position-y: 50%;
	&::placeholder {
		font-size: 13px;
		color: #000;
		font-family: $m_font; } }

.color-green {
	color: $green;
	margin: 0 4px; }

.no-margin {
	margin: 0!important; }

.header-container {
	display: flex;
	align-items: center;
	justify-content: space-around;
	max-width: 1670px;
	width: 100%;
	margin: 0 auto; }

.right__sup {
	display: flex;
	align-items: center;
	font-weight: 700;
	font-size: 14px;
	margin-bottom: 20px;
	#signUpButton {
		display: flex;
		align-items: center; } }

.header__right {
	img {
		margin-right: 7px; }
	a:hover {
		color: $green; } }

.header__logo {
	width: 228px;
	img {
		display: none; } }

.burger-button {
	display: none;
	cursor: pointer; }


.cart-button {
	margin-left: 50px;
	a {
		position: relative;
		display: flex;
		align-items: center;
		.cart-number {
			position: absolute;
			left: 14px;
			top: 0;
			font-size: 16px;
			color: #fff; } } }


.right__sub {
	display: flex;
	align-items: center;
	font-weight: 700;
	font-size: 13px;
	font-family: $m_font;
	justify-content: space-between; }

.contact-phone {
	a {
		display: flex;
		align-items: center; } }

.contact-schedule {
	display: flex;
	align-items: center; }

// Aside
.aside {
	border-right: 1px solid $green;
	width: 21.77%;
	min-width: 300px;
	z-index: 10;
	top: 0;
	left: 0;
	padding-top: 0px;
	min-height: 100%; }

.page-wrapper {
	display: flex; }

.aside__logo {
	text-align: center;
	height: 150px;
	padding: 35px 0;
	position: absolute;
	top: 0;
	left: 0;
	border-bottom: 1px solid $green;
	z-index: 200;
	background-color: #fff;
	width: 21.77%;
	min-width: 300px;
	border-right: 1px solid $green; }

.aside__inner {
	padding-left: 5%;
	padding-top: 50px;
	background-color: #fff;
	padding-bottom: 300px;
	position: relative; }

.aside__title {
	margin-bottom: 34px; }

.aside__nav {
	list-style: none;
	margin-bottom: 150px; }

.aside__nav-item a {
	font-size: 14px;
	font-weight: 700;
	line-height: 1; }

.aside__nav-item {
	padding: 10px 0;
	position: relative;
	transition: all .3s ease;
	margin-left: -23.444%;
	padding-left: 23.444%;
	position: static;
	&:hover,&:active {
		a::after {
			opacity: 1; } }
	& > a {
		position: relative; }
	& > a::after {
		content: "";
		display: block;
		opacity: 0;
		transition: all .3s ease;
		width: 0;
		height: 0;
		border: 10px solid transparent;
		border-right-color: #fff;
		position: absolute;
		right: 0;
		top: 50%;
		transform: translateY(-50%);
		z-index: 9999; }
	&:hover {
		color: #fff;
		background-color: $green;
		.aside__nav-sublist {
			display: flex; } }
	&:last-child {
		margin-bottom: 0; } }

.aside__tel a {
	display: block;
	font-size: 30px;
	font-weight: 700;
	font-family: $bebas; }

.aside__nav-sublist-image {
	background-color: #fff;
	text-align: center;
	width: 100%;
	margin-bottom: 15px;
	img {
		width: 100%;
		height: auto;
		max-width: 120px;
		max-height: 150px; } }



.aside__nav-sublist {
	display: none;
	position: absolute;
	left: 100%;
	top: 0;
	min-height: 600px;
	padding: 25px 0;
	box-sizing: content-box;
	max-height: calc(100vh - 200px);
	width: 77vw;
	background-color: #fff;
	min-width: 245px;
	list-style: none;
	box-shadow: $shadow2;
	flex-direction: column;
	flex-wrap: wrap; }

.aside__nav-sublist-item {
	display: block;
	position: relative;
	width: 250px;
	margin-bottom: 20px;
	border-bottom: 2px solid $green;
	padding-bottom: 10px;
	&:hover {
		.aside__nav-sublist2x {
			display: flex; } }
	a {
		display: block;
		padding: 5px 0 5px 20px;
		font-size: 16px;
		font-weight: 400;
		color: #000;
		&:hover {
			background-color: #fff;
			color: #000;
			text-decoration: underline; } }
	& > a {
		font-weight: 700;
		font-size: 16px;
		position: relative;
		color: #000;
		background-color: #fff;
		&:hover {
			text-decoration: none; }
		img {
			height: 100px;
			width: auto;
			display: block; } } }

.aside__nav-sublist2x {
	padding-bottom: 10px;
	display: flex;
	position: relative;
	background-color: #fff;
	list-style: none;
	flex-direction: column;
	.aside__nav-siblist2x-item {
		a {
			width: 100%;
			font-size: 13px; } }
	a {
		display: block;
		width: 200px;
		&:hover {
			background-color: #fff;
			color: #000; } } }

.aside__nab-siblist2x-more {
	text-align: start;
	padding: 2px 0;
	background-color: #fff;
	margin-bottom: 5px;
	a {
		font-weight: 700;
		color: #000;
		position: relative;
		&::after {
			content: "";
			width: 7px;
			height: 7px;
			border-bottom: 2px solid #000;
			border-left: 2px solid #000;
			transform: rotate(-45deg);
			position: absolute;
			left: 65px;
			top: 8px; }

		&:hover {
			text-decoration: underline; } } }

li.aside__nav-siblist2x-item {
	&.show {
		display: block !important; }

	&:nth-child(6) {
 } }		// background-color: red


// filter
.filter__discard {
	font-size: 20px;
	font-weight: 700;
	color: $green;
	display: flex;
	align-items: center;
	cursor: pointer;
	margin-bottom: 30px;
	img {
		margin-right: 8px; }
	&:hover {
		text-decoration: underline; } }

.aside__filter-title, .aside__price-range-title {
	font-weight: 700;
	font-size: 20px;
	margin-bottom: 20px; }

.aside__filter-input {
	width: 100%;
	background-color: #f1f1f1;
	font-size: 18px;
	border: 0;
	padding: 5px 10px;
	width: 268px;
	color: #000;
	font-family: $m_font;
	outline: none;
	box-shadow: $shadow;
	margin-bottom: 20px;
	&::placeholder {
		color: #000; } }

.aside__filter-item {
	display: flex;
	align-items: center;
	font-size: 20px;
	cursor: pointer;
	margin-bottom: 8px;
	img {
		margin-right: 10px; } }

.aside__filter {
	margin-bottom: 30px; }

.aside__price-range-title {
	margin-bottom: 10px; }


// Containers
.inner-wrapper {
	width: 78.23%;
	padding: 0 5%;
	margin: 50px auto 0; }
.main-container {
	width: 100%;
	max-width: 1246px;
	margin: 0 auto; }


.info-container {
	padding: 50px 0 50px 0px; }

.footer-container {
	max-width: 1510px;
	width: 100%;
	margin: 0 auto;
	padding: 38px 0; }

// SLider
.wrapper {
	display: flex;
	justify-content: space-between;
	width: 100%; }

.slick-dots {
	display: flex;
	justify-content: space-between;
	width: 160px;
	margin: 20px auto 0;
	li {
		width: 20px;
		height: 20px;
		border: 2px solid $green;
		border-radius: 50%;
		cursor: pointer;
		list-style: none;
		&.slick-active {
			background-color: $green; }
		button {
			background-color: transparent;
			font-size: 0;
			color: transparent;
			outline: none;
			border: 0; } } }

.dot {
	width: 20px;
	height: 20px;
	border: 2px solid $green;
	border-radius: 50%;
	cursor: pointer; }

.dot.active {
	background-color: $green; }

// Banners
.banners {
	margin-bottom: 50px;
	img {
		box-shadow: 0 3px 20px rgba(0,0,0,.2); } }

.banner-big {
	img {
		width: 100%;
		height: auto; } }

.banners--product {
	margin-bottom: 70px;
	position: relative;
	&:after {
		content: "";
		width: 1920px;
		height: 1px;
		position: absolute;
		bottom: -70px;
		right: -200px;
		background-color: $green; } }

.banners__title {
	font-size: 24px;
	margin-left: 50px;
	font-weight: 700;
	margin-bottom: 30px; }

.banners__wrapper {
	display: flex;
	justify-content: space-between;
	.slick-slide {
		background-size: contain !important;
		width: 200px;
		margin: 0 50px; }
	a {
 }		// box-shadow: 0 3px 20px rgba(0,0,0,0.5)
	img {
		width: initial;
		height: initial;
		display: none !important; } }

// Section
.section__header {
	text-align: center;
	position: relative;
	margin-bottom: 24px;
	&:before, &:after {
		content: "";
		height: 1px;
		width: 800px;
		background-color: $green;
		top: 50%;
		display: block;
		position: absolute;
		transform: translateY(-50%); }
	&:before {
		left: -275px; }
	&:after {
		right: -275px; } }

.section__header-newitems {
	&:before {
		left: -245px; }
	&:after {
		right: -245px; } }

.section__header--barrow {
	margin-bottom: 44px;
	&:before {
		left: -235px; }
	&:after {
		right: -235px; } }

.section__header--offer {
	margin-bottom: 44px;
	&:before {
		left: -355px; }
	&:after {
		right: -355px; } }
// sale
.sale {
	max-height: 600px; }
.product-card {
	padding: 20px;
	width: 274px;
	position: relative;
	box-shadow: $shadow;
	transition: all .3s ease; }

.sale__slider {
	margin-bottom: 50px; }

.sale-wrapper-outer {
	padding: 0 20px; }

.product-img {
	width: 205px;
	height: 146px;
	margin: 0 auto 10px;
	a img {
		display: block;
		width: 205px;
		height: auto; } }

.product-title {
	width: 186px;
	margin-bottom: 10px;
	min-height: 3em;
