	span {
		font-size: 18px;
		text-decoration: line-through; } }

.product-price {
	font-size: 20px;
	font-weight: 700;
	text-align: right;
	color: $green;
	font-family: $bebas;
	line-height: 1;
	span {
		font-size: 34px; } }

.product-button a {
	position: absolute;
	bottom: 0;
	right: 0;
	color: #fff;
	background-color: $green;
	font-family: $bebas;
	font-weight: 700;
	font-size: 20px;
	width: 137px;
	height: 60px;
