	margin-bottom: 50px; }

.new-items {
	position: relative;
	&:after {
		content: "";
		width: 2920px;
		height: 1px;
		position: absolute;
		bottom: -50px;
		right: -200px;
		background-color: $green; } }

// Info
.info-cont__inner {
	display: flex;
	justify-content: space-between;
	max-width: 1300px; }

.news-block__header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 15px; }

.news-block {
	width: 392px; }

.info-block__button a {
