	margin-bottom: 10px;
	a {
		font-size: 16px !important; }
	a:hover {
		text-decoration: underline; } }

.news-block__text, .articles-block__text, .video-block__text {
		font-size: 13px; }

.news-block__item {
	margin-bottom: 30px;
	&:last-child {
		margin-bottom: 0; } }

.articles-block {
	width: 392px; }

.articles-block__header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 44px; }

.articles-block__title {
	margin-top: 7px; }

.articles-block__topic a {
	font-size: 20px;
	font-weight: 700;
