	border-top: 1px solid $green;
	position: relative;
	z-index: 11;
	background-color: #fff; }

.about-us__inner {
	display: flex;
	flex-direction: column;
	justify-content: space-between; }

.about-us__title {
	margin-bottom: 24px; }

.about-us__text {
	line-height: 1.4; }

.about-us__info {
