// Footer
.footer {
	position: relative;
	z-index: 11;
	background-color: $green;
	color: #fff; }

.footer__inner {
	display: flex;
	justify-content: space-between; }

.footer__title {
	font-family: $bebas;
	font-size: 26px;
	font-weight: 700;
	margin-bottom: 26px; }

.footer__link {
	display: block;
	font-weight: 300;
	font-size: 20px;
	margin-bottom: 8px;
	&:last-child {
		margin-bottom: 0; }
