		text-decoration: underline; } }

p.footer__link:hover {
	text-decoration: none; }

.footer__payment {
	display: flex;
	justify-content: space-between;
	align-items: center; }

.footer__year {
	margin-top: 20px;
	text-align: center;
	font-weight: 300;
	font-size: 20px; }

// registration
.registration {
	display: flex;
	opacity: 0;
	position: fixed;
	top: -1500px;
	background-color: #fff;
	left: 21.77%;
	width: 78.23%;
	height: 85vh;
	z-index: 30;
	justify-content: center;
	align-items: center;
	transition: transform .5s ease-out, opacity .2s linear; }

.comment-modal {
	display: flex;
	opacity: 0;
	position: fixed;
	top: -1500px;
	background-color: #fff;
	left: 21.77%;
	width: 78.23%;
	height: 85vh;
	z-index: 30;
	justify-content: center;
	align-items: center;
	transition: transform .5s ease-out, opacity .2s linear;
	&.show {
		opacity: 1;
		// top: 150px
		transform: translateY(1650px); } }

.comment__title {
	margin-bottom: 60px;
	font-size: 24px; }

.registration.show {
	opacity: 1;
	// top: 150px
	transform: translateY(1650px); }

.reg__close {
	cursor: pointer;
	position: absolute;
	right: 150px;
	top: 50px; }

.modal-first {
	width: 322px;
	&.hide {
		display: none; }
	button {
		display: flex;
		width: 100%;
		border: 0;
		background-color: $green;
		font-family: $bebas;
		font-size: 20px;
		font-weight: 700;
		align-items: center;
		justify-content: center;
		color: #fff;
		height: 50px;
		cursor: pointer;
		box-shadow: $shadow;
		&:hover {
			box-shadow: none; } } }

.modal__buyers-button {
	margin-bottom: 50px; }

.comment-from__input {
	display: block;
	width: 100%;
	background-color: #f1f1f1;
	padding: 20px;
	margin-bottom: 20px;
	font-size: 100%;
	font-weight: 500;
	box-shadow: $shadow2;
	&[name=message] {
		height: 180px; }

	&:focus {
		outline: none;
		box-shadow: $shadow; }
	&::placeholder {
		font-family: $m_font; } }




.comment-form__info {
