/**
 * =====================================
 * CUSTOM STYLES
 * Roman Semenikhin / PUREWEB
 * =====================================
 */

// Homepage
.product-button {
	display: none; }
.contact-phone {
	position: relative;
	&:hover {
		.right__sub_contact_phones {
			visibility: visible; } } }
.right__sub_contact_phones {
	background: $green;
	transition: all .3s ease;
	top: 40px;
	visibility: hidden;
	position: absolute;
	color: #fff;
	width: 100%;
	&::after {
		content: "";
		display: block;
		width: 0;
		height: 0;
		border: 8px solid #fff;
		border-bottom-color: $green;
		top: -16px;
		left: 50%;
		transform: translateX(-50%);
		position: absolute; }
	li {
		padding: 10px 15px;
		&:hover {
			background: darken($green, 5%);
			a {
				color: #fff; } } }
	a {
		justify-content: center;
		transition: none; } }
.news__block {
 	margin-bottom: 20px; }

.aside__soc_links {
	display: flex;
	a {
		font-size: 22px;
		margin-right: 10px;
		i {
			transition: all .3s ease; } }

	.fa-viber:hover {
		color: #7C519E; }

	.fa-telegram:hover {
		color: #2CA4D8; } }

.stocks {
	display: table;
	margin-left: auto;
	padding: 10px 15px;
	border: 1px solid $green;
	border-radius: 5px; }

// Breadcrumbs
.breadcrumbs-nav {
	ul {
		display: flex;
		flex-wrap: wrap;
		li {
			list-style: none;
			margin-right: 10px;
			position: relative;
			&::after {
				content: "›";
				display: block;
				color: rgb(55, 93, 129);
				position: absolute;
				top: 0;
				right: -8px; } } } }

// Cards
.discount {
	&__price {
		color: red; }

	.old__price {
		font-size: 26px; } }

.category__inner, .offer__slider, .sale-wrapper, .hits-wrapper, .new-items-wrapper, {
		&__price {
			font-size: 34px; }

		.discount__number {
			font-size: 18px;
			background: #FF7373;
			color: #fff;
			font-family: $m_font;
			position: absolute;
			top: 0;
			right: 0;
			font-size: 16px;
			padding: 5px 10px; }

		.old__price {
			font-size: 20px;
			text-decoration: line-through; } }

// Product Cart
.product-tabs__tab-content {
  display: none /* по умолчанию прячем все блоки */; }

.product-tabs__tab-content.active {
  display: block /* по умолчанию показываем нужный блок */; }


// News Page
.allnews__date {
	font-size: 12px;
	margin-top: 10px; }

